<script>
import { Vue, ref, onMounted } from 'vue'
import { useRouter } from "vue-router"
import Header from "../../components/global/header/HeaderArea.vue"
import { getUrlId, monthHandle, monthHandle2, monthHandle3, errorHandle } from '@/utils/handle'
import { apiGetWorkOrderInfo, apiGetCaretakerList, apiGetJobType, apiSendWorkOrder, apiDeleteWorkOrderInfo } from "@/api/api"
import { useJobStore } from '@/store/global'
import { Toast, ToastConfirm } from '@/components/global/swal'

export default {
  setup() {
    const jobStore = useJobStore()
    const router = useRouter()
    const urlId = getUrlId().urlId

    let orderData = ref([])
    // 工單回覆狀態sta(0=未讀取,1=已讀取,2=接受,3=拒絕)
    let orderSta = []


    let jobTypeList = []
    let jobType = {}
    let jobTypeId = 1
    let favData = ref([])
    let workOrder = ref({ "workOrderId": urlId, "caretakerResumeId": [] })

    const loadFavData = async () => {
      if (jobStore.jobTypeList == undefined) {
        jobTypeList = await apiGetJobType()
        jobTypeList = jobTypeList.data.data
        jobStore.jobTypeList = jobTypeList.value
      } else {
        jobTypeList = jobStore.jobTypeList
      }
      // jobType = requireList.value.filter(x => x.id == postData.value.serviceId)
      jobTypeId = jobTypeList.filter(x => x.name == orderData.value.employerService.name)
      jobTypeId = jobTypeId[0].id
      //最愛看護列表
      favData.value = await apiGetCaretakerList({ page: 1, limit: 100, isFavorite: 1, jobTypeId: jobTypeId })
      favData.value = favData.value.data.data
      // console.log(favData.value)
      //過濾邀約過得看護
      let invited = []
      orderData.value.careTakerList.forEach(item => {
        invited.push(item.id)
      })
      favData.value = favData.value.filter(x => !invited.includes(x.id))
    }
    const checkInvite = async () => {
      // console.log(workOrder.value)
      let msg = await apiSendWorkOrder(workOrder.value)
      if (msg.status == true) {
        Toast("已發送邀約")
        try {
          let newData = await apiGetWorkOrderInfo({ id: urlId })
          orderData.value.careTakerList = newData.data.careTakerList
        } catch (error) {
          console.log(error)
        }
      } else {
        let errorMsg = errorHandle(msg)
        Toast(errorMsg)
      }
    }


    onMounted(async () => {
      try {
        orderData.value = await apiGetWorkOrderInfo({ id: urlId })
        orderData.value = orderData.value.data
        //看護回覆狀態
        orderData.value.careTakerList.forEach((item, index) => {
          orderSta.push(item.replyStatus)
        })
        orderData.value.orderSta = orderSta
        //處理日期
        let res = monthHandle(orderData.value.workdays)
        orderData.value.newDates = res.newDates
        orderData.value.info = res.info
        orderData.value.time = res.time
        orderData.value.monthInfo = Object.keys(orderData.value.info)
        // languageInfo
        orderData.value.employerService.languageInfo = []
        if (orderData.value.employerService.language?.length > 0) {
          orderData.value.employerService.language.forEach(item => {
            orderData.value.employerService.languageInfo.push(item.name)
          })
        }
        //licenseInfo
        orderData.value.employerService.licenseInfo = []
        if (orderData.value.employerService.license?.length > 0) {
          orderData.value.employerService.license.forEach(item => {
            orderData.value.employerService.licenseInfo.push(item.name)
          })
        }
        //eatTypeInfo
        // orderData.value.disabledPerson.eatTypeInfo = []
        // if (orderData.value.disabledPerson.eatType?.length > 0) {
        //   orderData.value.disabledPerson.eatType.forEach(item => {
        //     orderData.value.disabledPerson.eatTypeInfo.push(item.name)
        //   })
        // }
        //abilityToActInfo
        orderData.value.disabledPerson.abilityToActInfo = []
        if (orderData.value.disabledPerson.abilityToAct?.length > 0) {
          orderData.value.disabledPerson.abilityToAct.forEach(item => {
            orderData.value.disabledPerson.abilityToActInfo.push(item.name);
          })
        }
        //servicePlaceInfo
        orderData.value.disabledPerson.servicePlaceInfo = orderData.value.disabledPerson.servicePlace.city.name + orderData.value.disabledPerson.servicePlace.area.name + " " + orderData.value.disabledPerson.servicePlace.place.name
        //serviceInfo
        orderData.value.employerService.serviceInfo = []
        if (orderData.value.employerService.service?.length > 0) {
          orderData.value.employerService.service.forEach(item => {
            orderData.value.employerService.serviceInfo.push(item.name)
          })
        }
        //看護日期處理
        orderData.value.careTakerList.forEach(item => {
          if (item.cancelDate) {
            if (item.cancelDate?.length > 0) {
              let res = monthHandle3(item.cancelDate)
              item.cancelMonthInfo = res.monthInfo
              item.cancelDayInfo = res.dayInfo
            }
          }
          if (item.checkDate) {
            let res2 = monthHandle3(item.checkDate)
            item.checkMonthInfo = res2.monthInfo
            item.checkDayInfo = res2.dayInfo
          }
        })
      } catch (error) {
        console.log(error)
      }

      // console.log(orderData.value)
    })

    const goto = (val) => {
      router.push({ path: val })
    }
    const deleteOrder = async () => {
      const result = await ToastConfirm("確定刪除此筆資料")
      if (result == true) {
        try {
          let msg = await apiDeleteWorkOrderInfo({ id: urlId })
          if (msg.status == true) {
            Toast("刪除成功")
            router.push({ path: '/order/list' })
          } else {
            Toast(msg.message)
          }
        } catch (error) {
          console.log(error)
        }

      }
    }

    return { orderData, goto, urlId, loadFavData, favData, checkInvite, workOrder, deleteOrder }
  },
  components: {
    Header
  },
};

</script>

<template>
  <Header title="工單內容" backToPath="/order/list" />
  <div class="orderInfo">
    <section class="c-main">
      <div class="detail-container">
        <div class="item-container">
          <div class="item-title">照顧日期時段</div>
          <div class="item-content">
            <div class="main2">
              <div class="title2">照顧日期</div>
              <div class="content2">{{ orderData?.monthInfo?.[0] }}/{{
                  orderData?.info?.[orderData?.monthInfo?.[0]].join('、')
              }}</div>
              <div class="content2" v-if="orderData?.monthInfo?.length == 2">{{ orderData?.monthInfo?.[1] }}/{{
                  orderData?.info?.[orderData?.monthInfo?.[1]].join('、')
              }}</div>
            </div>
            <div class="main2">
              <div class="title2">照顧時段</div>
              <div class="content2">{{ orderData.time }}</div>
            </div>
          </div>
        </div>
        <div class="item-container" v-if="orderData.employerService">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse1">{{
              orderData.employerService.name
          }}</div>
          <div class="item-content collapse" id="collapse1">
            <div class="main1">
              <div class="title1">性別</div>
              <div class="content1">{{ orderData.employerService.gender == "Male" ? "男性" :
                  (orderData.employerService.gender == "Female") ? "女性" : "不指定"
              }}</div>
            </div>
            <div class="main1">
              <div class="title1">年資</div>
              <div class="content1">{{ orderData.employerService?.seniority?.name }}</div>
            </div>
            <div class="main1">
              <div class="title1">語言</div>
              <div class="content1">{{ orderData.employerService.languageInfo.join('、') }}</div>
            </div>
            <div class="main2">
              <div class="title2">具備證照</div>
              <div class="content2" v-if="(orderData?.employerService?.licenseInfo?.length != 0)">{{
                  orderData?.employerService?.licenseInfo?.join('、')
              }}</div>
              <div class="content2" v-if="(orderData?.employerService?.licenseInfo?.length == 0)">無</div>
            </div>
          </div>
        </div>
        <div class="item-container" v-if="orderData?.disabledPerson">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse2">被照護者資訊</div>
          <div class="item-content collapse" id="collapse2">
            <div>
              <div class="main1">
                <div class="title1">名稱</div>
                <div class="content1">{{ orderData.disabledPerson.name }}</div>
              </div>
              <div class="main1">
                <div class="title1">性別</div>
                <div class="content1">{{ orderData.disabledPerson.gender == 'Female' ? '女性' : '男性' }}</div>
              </div>
              <div class="main1">
                <div class="title1">年齡</div>
                <div class="content1">{{ orderData.disabledPerson.age }}歲</div>
              </div>
              <div class="main1">
                <div class="title1">身高</div>
                <div class="content1">{{ orderData.disabledPerson.height }}公分</div>
              </div>
              <div class="main1">
                <div class="title1">體重</div>
                <div class="content1">{{ orderData.disabledPerson.weight }}公斤</div>
              </div>
              <div class="main1">
                <div class="title1">關係</div>
                <div class="content1">{{ orderData.disabledPerson.relationship }}</div>
              </div>
              <!-- <div class="main2">
                <div class="title2">是否有傳染力</div>
                <div class="content2">{{ orderData.disabledPerson.isContagious ? "是" : "否" }}</div>
              </div>
              <div class="main2">
                <div class="title2">飲食類型</div>
                <div class="content2" v-if="(orderData?.disabledPerson?.eatTypeInfo?.length != 0)">{{
                    orderData?.disabledPerson?.eatTypeInfo?.join('、')
                }}</div>
                <div class="content2" v-if="(orderData?.disabledPerson?.eatTypeInfo?.length == 0)">無</div>
              </div> -->
              <div class="main2">
                <div class="title2">行動能力</div>
                <div class="content2" v-if="(orderData?.disabledPerson?.abilityToActInfo?.length != 0)">{{
                    orderData?.disabledPerson?.abilityToActInfo?.join('、')
                }}</div>
                <div class="content2" v-if="(orderData?.disabledPerson?.abilityToActInfo?.length == 0)">無</div>

              </div>
              <div class="main2">
                <div class="title2">照顧地點</div>
                <div class="content2">{{ orderData.disabledPerson.servicePlaceInfo }}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="item-container">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse3">病症</div>
          <div class="item-content collapse" id="collapse3">
            <div class="main2" v-for="item in orderData?.disabledPerson?.disease" :key="item.id">
              <div class="title2">{{ item.diseaseName }}</div>
              <div class="content2">{{ item.symptom.join('、') }}</div>
            </div>
          </div>
        </div>
        <div class="item-container" v-if="orderData?.employerService">
          <div class="item-title item-link collapsed" data-bs-toggle="collapse" data-bs-target="#collapse4">服務需求</div>
          <div class="item-content collapse" id="collapse4">
            <div class="content2">{{ orderData.employerService?.serviceInfo?.join('、') }}</div>
          </div>
        </div>
        <div class="item-container">
          <div class="item-title">已回復邀約<a href="javascript:void(0);" class="btn btn-add" data-bs-toggle="modal"
              data-bs-target="#openCourse" @click="loadFavData">新增邀約</a></div>
          <div class="item-content">
            <div class="caregiver-outer">
              <div class="card" v-for="item in orderData.careTakerList" :key="item.id">
                <div class="card-header">
                  <a @click="goto(`/order/info/${urlId}/${item.id}`)" class="card-link">
                    <img :src="item.avatarUrl" class="card-img" v-if="(item.avatarUrl)" onerror="this.onerror=null; this.src='https://fakeimg.pl/91x121/'">
                    <img src="@/assets/image/noavatar.jpg" class="card-img" v-if="(!item.avatarUrl)">
                  </a>
                </div>
                <div class="card-body">
                  <a @click="goto(`/order/info/${urlId}/${item.id}`)" class="card-link">
                    <div class="card-title">{{ item.name }}({{ item.gender == "Male" ? "男性" : "女性" }}/{{ item.age }}歲/{{
                        item.height
                    }}cm/{{ item.weight }}kg)</div>
                    <div class="card-date">
                      <div class="d-flex" v-if="(item.replyStatus == 2)">
                        <div class="col-left">接受日期：</div>
                        <div class="col-right">{{ item?.checkMonthInfo[0] }}/<span class="yes">{{
                            item.checkDayInfo[item?.checkMonthInfo[0]].join('、')
                        }}</span>
                          <div v-if="item.checkMonthInfo?.length == 2">{{ item?.checkMonthInfo[1] }}/<span class="yes">{{
                              item.checkDayInfo[item?.checkMonthInfo[1]].join('、')
                          }}</span></div>
                        </div>
                      </div>
                      <div class="d-flex" v-if="(item.replyStatus == 2)">
                        <div class="col-left">取消日期：</div>
                        <div class="col-right" v-if="(item.cancelDate == 0)">無
                        </div>
                        <div class="col-right" v-if="(item.cancelDate != 0)">{{ item.cancelMonthInfo[0] }}/<span
                            class="no">{{
                                item.cancelDayInfo[item.cancelMonthInfo[0]].join('、')
                            }}</span>
                          <div v-if="item.cancelMonthInfo.length == 2">{{ item.cancelMonthInfo[1] }}/<span class="no">{{
                              item.cancelDayInfo[item.cancelMonthInfo[1]].join('、')
                          }}</span></div>
                        </div>
                      </div>
                      <div class="d-flex" v-if="(item.replyStatus != 2)">
                        <div class="col-left">照顧日期：</div>
                        <div class="col-right">{{ orderData?.monthInfo?.[0]
                        }}/{{ orderData?.info?.[orderData?.monthInfo?.[0]].join('、') }}
                          <div v-if="orderData?.monthInfo?.length == 2">{{
                              orderData?.monthInfo?.[1]
                          }}/{{ orderData?.info?.[orderData?.monthInfo?.[1]].join('、') }}</div>
                        </div>
                      </div>
                    </div>
                    <div class="card-time">照顧時段：{{ item.startTime }}~{{ item.endTime }}</div>
                  </a>
                  <div class="card-state">
                    <a class="btn btn-state" v-if="(item.replyStatus == 0)">未讀取</a>
                    <a class="btn btn-state" v-if="(item.replyStatus == 1)">已讀取</a>
                    <a class="btn btn-wait" v-if="(item.replyStatus == 2)">已接受</a>
                    <a class="btn btn-wait" v-if="(item.replyStatus == 3)">已拒絕</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="mt-4b btn-container">
          <div class="row">
            <div class="col" v-if="(orderData.workOrderStatus == 1)">
              <button class="btn btn-outline-primary" type="button" @click="goto(`/order/edit/${urlId}`)">修改資料</button>
            </div>
            <div class="col">
              <button class="btn btn-outline-primary" type="button" @click="deleteOrder">刪除資料</button>
            </div>
          </div>
        </div>
        <!-- 新增邀約彈窗 -->
        <div class="modal fade" id="openCourse" tabindex="-1" aria-labelledby="openModalLabel" aria-hidden="true">
          <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
              <div class="modal-body">
                <div class="noDataAlert">最愛照護人員</div>
                <div class="caregiver-outer" v-if="(favData.length > 0)">
                  <div class="card" v-for="item in favData" :key="item.id">
                    <div class="card-header">
                      <a href="javascript:void(0);" class="card-link">
                        <img :src=item.avatarImgUrl class="card-img" v-if="(item.avatarImgUrl)" onerror="this.onerror=null; this.src='https://fakeimg.pl/150x200/'">
                        <img src="@/assets/image/noavatar.jpg" class="card-img" v-if="(!item.avatarImgUrl)">
                      </a>
                    </div>
                    <div class="card-body">
                      <a href="javascript:void(0);" class="card-link">
                        <div class="card-title">{{ item.name }}<i
                            :class="item.gender == '男性' ? 'far fa-venus' : 'far fa-mars'"></i></div>
                        <div class="card-info">{{ item.age }}歲/{{ item.height }}cm/{{ item.weight }}kg 年資{{
                            item.Seniority
                        }}</div>
                        <div class="card-license">{{ item.license.join('、') }}</div>
                        <div class="card-text" v-if="(item.experience.length > 0)">照顧經驗：{{ item.experience.join('、') }}
                        </div>
                        <div class="card-text" v-if="(item.experience.length == 0)">照顧經驗：無</div>
                      </a>
                      <input type="checkbox" :id=item.id :value=item.id class="btn btn-delete checkBtn"
                        v-model="workOrder.caretakerResumeId">
                    </div>
                  </div>
                </div>
                <div class="caregiver-outer" v-if="(favData.length == 0)">
                  <div class="noDataAlert">請先新增最愛看護清單</div>
                </div>
              </div>
              <div class="modal-footer">
                <div class="btn-container">
                  <div class="row" v-if="(favData.length != 0)">
                    <div class="col">
                      <button class="btn btn-outline-primary" type="button" data-bs-dismiss="modal"
                        @click="checkInvite">送出邀約</button>
                    </div>
                    <div class="col">
                      <button class="btn btn-outline-primary" type="button" data-bs-dismiss="modal">取消邀約</button>
                    </div>
                  </div>
                  <div class="row" v-if="(favData.length == 0)">
                    <div class="col">
                      <button class="btn btn-outline-primary" type="button" data-bs-dismiss="modal">確定</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>



</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.orderInfo {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}

.main {
  padding-top: 22px;
  padding-left: 22px;
  padding-right: 22px;
}

.main1 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 5px;
  margin-bottom: 5px;

  .title1 {
    font-size: 14px;
    font-weight: 400;
    color: #909090;
    margin-right: 14px;

  }

  .content1 {
    font-size: 16px;
    font-weight: 400;
    color: #333333;
  }
}

.main2 {
  margin-top: 10px;
  margin-bottom: 10px;

  .title2 {
    font-size: 14px;
    font-weight: 400;
    color: nth($secondary-color, 1);
  }

  .content2 {
    font-size: 16px;
    font-weight: 400;
    color: #333333;
  }

}

.modal-body {
  max-height: 50vh;
  overflow-y: scroll;
}

.modal-body::-webkit-scrollbar {
  display: none;
}

.checkBtn {
  margin: 1rem;
}

.form-check-inline {
  margin-left: 1rem;
}

.noDataAlert {
  font-size: 1.25rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
  margin: 0.8rem;
}
</style>